.alert {
    position: absolute;
    left: 43%;
    top: 6%;
    background-color: red;
    width: 150px;
    color: white;
    border-radius: 4px;
}

.alert-enter-active {
    animation: task 800ms ease-in alternate-reverse;
}

@keyframes task {
    0% {
        transform: translateX(0);
    }

    30% {
        opacity: 0.7;
        transform: translateX(30px);
    }

    100% {
        opacity: 0;
        transform: translateX(-200px);
    }
}

.alert-exit-active {
    animation: taskBack 800ms ease-in;
}

@keyframes taskBack {
    0% {
        transform: translateX(0);
    }

    30% {
        opacity: 0.7;
        transform: translateX(30px);
    }

    100% {
        opacity: 0;
        transform: translateX(-200px);
    }
}